import Vue from 'vue/dist/vue.common.js';

window.Vue = Vue;

import elementUi from 'element-ui';

// import editor from './components/editor/dist/vue-html5-editor.js';

import editor from './components/editor/index.js';


/**/
Vue.use(elementUi);


Vue.use(editor, {
    //global component name
    name: 'editor',
    //custom icon class of built-in modules,default using font-awesome
    icons: {
        text: 'fa fa-pencil',
        color: 'fa fa-paint-brush',
        font: 'fa fa-font',
        align: 'fa fa-align-justify',
        list: 'fa fa-list',
        link: 'fa fa-chain',
        unlink: 'fa fa-chain-broken',
        tabulation: 'fa fa-table',
        image: 'fa fa-file-image-o',
        hr: 'fa fa-minus',
        eraser: 'fa fa-eraser',
        undo: 'fa-undo fa',
        'full-screen': 'fa fa-arrows-alt',
        info: 'fa fa-info',
    },
    //config image module
    image: {
        //Url of the server-side,default null and convert image to base64
        server: null,
        //the name for file field in multipart request
        fieldName: 'image',
        //max file size
        sizeLimit: 512 * 1024,
        // default true,if set to true,the image will resize by localResizeIMG (https://github.com/think2011/localResizeIMG)
        compress: true,
        //follows are options of localResizeIMG
        width: 1600,
        height: 1600,
        quality: 80,
        //handle response data，return image url
        uploadHandler(responseText){
            //default accept json data like  {ok:false,msg:'unexpected'} or {ok:true,data:'image url'}
            var json = JSON.parse(responseText)
            if (!json.ok) {
                alert(json.msg)
            } else {
                return json.data
            }
        }
    },
    //default en-us, en-us and zh-cn are built-in
    language: 'en-us',
    i18n: {
        //specify your language here
				'en-us': {
				    'align': 'align',
				    'image': 'image',
				    'list': 'list',
				    'link': 'link',
				    'unlink': 'unlink',
				    'table': 'table',
				    'font': 'font',
				    'full screen': 'full screen',
				    'text': 'text',
				    'eraser': 'remove format',
				    'info': 'info',
				    'color': 'color',
				    'please enter a url': 'please enter a url',
				    'create link': 'create link',
				    'bold': 'bold',
				    'italic': 'italic',
				    'underline': 'underline',
				    'strike through': 'strike through',
				    'subscript': 'subscript',
				    'superscript': 'superscript',
				    'heading': 'heading',
				    'font name': 'font name',
				    'font size': 'font size',
				    'left justify': 'left justify',
				    'center justify': 'center justify',
				    'right justify': 'right justify',
				    'ordered list': 'ordered list',
				    'unordered list': 'unordered list',
				    'fore color': 'fore color',
				    'background color': 'background color',
				    'row count': 'row count',
				    'column count': 'column count',
				    'save': 'save',
				    'upload': 'upload',
				    'progress': 'progress',
				    'unknown': 'unknown',
				    'please wait': 'please wait',
				    'error': 'error',
				    'abort': 'abort',
				    'reset': 'reset',
				    'hr': 'horizontal rule',
				    'undo': 'undo',
				    'line height': 'line height',
				    'exceed size limit': 'exceed size limit'
				}
    },
    //the modules you don't want
    hiddenModules: [],
    //keep only the modules you want and customize the order.
    //can be used with hiddenModules together
    visibleModules: [
        'text',
        'color',
        'font',
        'align',
        'list',
        'link',
        'unlink',
        'tabulation',
        'image',
        'hr',
        'eraser',
        'undo',
        'full-screen',
        'info',
    ],
    //extended modules
    modules: {
        //omit,reference to source code of build-in modules
    }
})








